@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,900);
[dir] .wrapper_css{
    background: #15B769
}

.white_text{
color: #ffffff !important;
}
.white_btn {
    font-size: 12px !important;
    color: white;
}
[dir] .white_btn {
    border-radius: 30px !important;
    border: 1px solid #fff !important;
    padding: 8px 11px !important;
    margin-top: 5px !important;
}
[dir=ltr] .white_btn {
    background: transparent none repeat scroll 0 0 !important;
}
[dir=rtl] .white_btn {
    background: transparent none repeat scroll 100% 0 !important;
}
[dir] .white_background{
    background: #ffffff !important;
}
[dir] .pricing_background{
    background-color: #0fb769 !important;
}
.app_store{
    width: 130px;
    height: 50px;
}
[dir] .signupBackground {
    background: #98DBA9;
}

.signup_btn{
    font-size: 12px !important;
    /* height: 10% !important;*/
    width: 10% !important; 
}

[dir] .signup_btn{
    border-radius: 30px !important;
    border: 1px solid #fff !important;
    padding: 8px 8px !important;
    margin-top: 5px !important; 
}

[dir=ltr] .signup_btn{
    background: #295940 none repeat scroll 0 0!important; 
}

[dir=rtl] .signup_btn{
    background: #295940 none repeat scroll 100% 0!important; 
}
[dir] .footer_background{
    background: #295940;
}
.white_appstore {
    width: 100px;
    height: 40px;
}
